<template lang="pug">
    div.arged-content
        div.arged-item    
            div.arged-info
                div.arged-info-top
                    p.level(v-text="pageData.leve_name")
                    img.icon_medal(src="../../assets/img/icon_medal.png")
                    p.exp_val
                        span.c-red(v-text="pageData.u_score")
                        template(v-if="pageData.up_score != '满级'")
                            span(v-text="`/${pageData.up_score}`")
                    div.scroll
                        div.theme_color
                div.arged-info-bottom
                    p 观看直播就可以获得经验值哦
                    p 想自己直播？没问题，直播经验更加多
                    p 不想等待？礼物刷起来，刷得越多，升级越快
</template>
<script>
import { Toast } from 'mint-ui'
export default {
    name: 'userGrade',
    data () {
        return {
            pageData: ''
        }
    },
    created () {
        this.getData();
    },
    methods: {
        getData() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_centerGrade' : this.api.user_centerGrade();
            this.axios.get(axiosUrl).then(res => {
                if(res.data.status == 1) {
                     this.pageData = res.data;
                }
                else {
                    Toast(res.data.error || '操作失败');
                }
            })
            .catch(err => {
                Toast(err || '网络异常，请求失败')
            });
        }
    }
}
</script>
<style lang="less" scoped>
@import '../../assets/css/variable.less';
.arged-content {
    height: 100%;
    width: 100%;
    .arged-item {
        position: absolute;
        width: 100%;
        height: 100%;
        overflow: hidden;
        background: @color-theme;
        height: 100%;
        padding: 1.25rem 1rem 1rem;
        .arged-info {
            border-radius: .25rem;
            overflow: hidden;
            background-color: #fff;
            .arged-info-top {
                padding: 1rem;
                .level {
                    color: #fff;
                    width: 85%;
                    height: 2rem;
                    line-height: 1.7rem;
                    text-align: center;
                    background: url(../../assets/img/y1.png) no-repeat;
                    background-size: 100% 100%;
                    margin: 0 auto 1rem;
                }
                .icon_medal {
                    width: 100%;
                }
                .scroll {
                    width: 100%;
                    height: 5px;
                    border-radius: 5px;
                    margin-top: .5rem;
                    position: relative;
                    overflow: hidden;
                    background: #aaa;
                    .theme_color {
                        width: 50%;
                        height: 5px;
                        border-radius: 5px;
                        position: absolute;
                        top: 0;
                        left: 0;
                        overflow: hidden;
                        background: @color-theme;
                    }
                }
                .exp_val {
                    color: #999;
                    font-size: .6rem;
                    text-align: center;
                    .c-red{
                        color: @color-red;
                    }
                }
            }
            .arged-info-bottom {
                padding: 1.4rem .5rem 1.2rem;
                border-top: 1px dashed #eee;
                color: #999;
                text-align: center;
            }
        }
    }
}
</style>




